import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { fetchArticles, editeArticle } from "../redux/actions/articlesActions";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  TextField,
  Divider,
  Grid,
  IconButton,
  Checkbox,
  FormControlLabel,
} from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";
import Card from "@mui/material/Card";
import CardMedia from "@mui/material/CardMedia";
import CloudUploadIcon from "@mui/icons-material/CloudUpload";
import MenuItem from "@mui/material/MenuItem";
import Select from "@mui/material/Select";
import InputLabel from "@mui/material/InputLabel";
import FormControl from "@mui/material/FormControl";
import axios from "axios";

const EditArticleDialog = ({ open, onClose, onAdd, initialArticle }) => {
  const categories = useSelector((state) => state.categories);

  const [codeArticle, setCodeArticle] = useState("");
  const [description, setDescription] = useState("");
  const [designation, setDesignation] = useState("");
  const [files, setFiles] = useState("");
  const [filesInititlal, setFilesInititlal] = useState("");
  const [visible, setVisible] = useState("");
  const [codeCat, setCodeCat] = useState("");
  const [disabledButton, setDisabledButton] = useState(false);
  const [prix, setPrix] = useState("");
  const [imagePreview, setImagePreview] = useState("");

  useEffect(() => {
    setCodeArticle(initialArticle && initialArticle.CodeArt);
    setDescription(initialArticle && initialArticle.Descrip);
    setDesignation(initialArticle && initialArticle.LibArt);
    setFilesInititlal(initialArticle && initialArticle.image_web);
    setVisible(initialArticle && initialArticle.visible_web);
    setCodeCat(initialArticle && initialArticle.CodeCat);
    setPrix(initialArticle && initialArticle.prix1);
    setImagePreview(initialArticle && initialArticle.image_web);
  }, [initialArticle]);

  const dispatch = useDispatch();

  const handleImageChange = (e) => {
    const file = e.target.files[0];
    setFiles(file);

    // Set image preview URL
    const previewURL = URL.createObjectURL(file);
    setImagePreview(previewURL);
  };

  const handleSubmit = async () => {
    try {
      setDisabledButton(true);
      let EditArticle = {};

      if (!files && !filesInititlal) {
        alert("Please upload an image first!");
        return;
      }

      if (files.name && files.name.length > 50) {
        alert("Le nom de l'image doit tre court (infrieur  50 caractres).");
        return;
      }

      if (filesInititlal) {
        EditArticle = {
          CodeArt: codeArticle,
          Descrip: description,
          LibArt: designation,
          CodeCat: codeCat,
          prix1: prix,
          image_web: filesInititlal,
          visible_web: visible === true ? 1 : 0,
        };

        await dispatch(editeArticle(codeArticle, EditArticle)).then(() => {
          resetForm();
          onAdd();
        });
      }

      if (files) {
        const formData = new FormData();
        formData.append("image", files);
        formData.append("key", "881f6a30630e96747675ad6aa76cb50b");

        const config = {
          headers: {
            "content-type": "multipart/form-data",
          },
        };

        const response = await axios.post(
          "https://api.imgbb.com/1/upload",
          formData,
          config
        );

        const imageUrl = response.data.data.url;

        EditArticle = {
          CodeArt: codeArticle,
          Descrip: description,
          LibArt: designation,
          CodeCat: codeCat,
          prix1: prix,
          image_web: imageUrl,
          visible_web: visible === true ? 1 : 0,
        };

        await dispatch(editeArticle(codeArticle, EditArticle)).then(() => {
          resetForm();
          onAdd();
        });
      }
    } catch (error) {
      console.log(error);
    } finally {
      setDisabledButton(false);
    }
  };

  const resetForm = () => {
    setCodeArticle("");
    setDescription("");
    setDesignation("");
    setFiles("");
    setVisible("");
    setCodeCat("");
    setPrix("");
    setImagePreview("");
  };

  const handleClose = () => {
    resetForm();
    onClose();
  };

  useEffect(() => {
    dispatch(fetchArticles());
  }, []);

  return (
    <Dialog open={open} onClose={handleClose}>
      <DialogTitle sx={{ fontWeight: "bold" }}>
        Edit un article
        <IconButton
          aria-label="close"
          onClick={onClose}
          sx={{ position: "absolute", right: 8, top: 8 }}
        >
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <Divider />
      <DialogContent>
        <DialogContentText>
          Veuillez entrer les dtails pour le nouvel article :
        </DialogContentText>
        <TextField
          disabled={true}
          label="Code"
          fullWidth
          margin="normal"
          size="small"
          value={codeArticle}
          onChange={(e) => setCodeArticle(e.target.value)}
        />
        <TextField
          label="Description"
          fullWidth
          margin="normal"
          size="small"
          value={description}
          onChange={(e) => setDescription(e.target.value)}
        />
        <TextField
          label="Dsignation"
          fullWidth
          margin="normal"
          size="small"
          value={designation}
          onChange={(e) => setDesignation(e.target.value)}
        />
        <Grid
          container
          alignItems="center"
          justifyContent="space-between"
          spacing={1}
        >
          <FormControl
            sx={{ margin: "17px 0 10px 7px", width: "230px" }}
            size="small"
          >
            <InputLabel id="demo-select-small-label">
              Code Categorie
            </InputLabel>
            <Select
              labelId="demo-select-small-label"
              id="demo-select-small"
              value={codeCat}
              label="Code Categorie"
              onChange={(event) => setCodeCat(event.target.value)}
              focused
              autoFocus
            >
              {categories &&
                categories.categories &&
                categories.categories.map((val, index) => (
                  <MenuItem key={index} value={val.CodeCat}>
                    {val.DesCat}
                  </MenuItem>
                ))}
            </Select>
          </FormControl>

          <TextField
            label="Prix"
            margin="normal"
            size="small"
            value={prix}
            onChange={(e) => setPrix(e.target.value)}
          />
        </Grid>

        <Grid
          container
          alignItems="center"
          justifyContent="space-between"
          spacing={1}
        >
          <Grid item>
            <FormControlLabel
              control={
                <Checkbox
                  checked={visible}
                  onChange={(e) => setVisible(e.target.checked)}
                  color="primary"
                />
              }
              label="Visible"
            />
            <input
              type="file"
              accept="image/*"
              style={{ display: "none" }}
              id="file-input"
              onChange={handleImageChange}
            />
            <label htmlFor="file-input">
              <IconButton component="span">
                <CloudUploadIcon />
              </IconButton>
            </label>
            <span>Upload Image</span>
          </Grid>

          {imagePreview && (
            <Grid item>
              <Card>
                <CardMedia
                  component="img"
                  alt="Sample Image"
                  height="140"
                  image={imagePreview}
                  style={{ maxHeight: "100px", width: "auto" }}
                />
              </Card>
            </Grid>
          )}
        </Grid>
      </DialogContent>
      <Divider />
      <DialogActions>
        <Button
          onClick={handleClose}
          color="primary"
          style={{ backgroundColor: "#6C757D", color: "white" }}
        >
          Annuler
        </Button>
        <Button
          disabled={disabledButton}
          onClick={handleSubmit}
          variant="contained"
        >
          Enregistrer
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default EditArticleDialog;
